
import { defineComponent } from "vue";

const choosePage = defineComponent({
  data() {
    return {
      page: 0,
      shorts: ["视频", "链接", "参考", "管理"],
      titles: ["视频导引", "相关链接", "资料参考", "管理工具"],
    };
  },
  methods: {
    update(index: number) {
      this.page = index;
      this.$emit("select-page", index);
    },
  },
});
export default choosePage;
