___CSS_LOADER_EXPORT___.push([module.id, "\nfooter[data-v-2df4c534] {\r\n  color: inherit;\r\n  vertical-align: bottom;\r\n  text-align: center;\n}\nsmall[data-v-2df4c534] {\r\n  font-size: 0.8em;\n}\r\n", "",{"version":3,"sources":["webpack://./src/footer.vue"],"names":[],"mappings":";AA6FA;EACE,cAAc;EACd,sBAAsB;EACtB,kBAAkB;AACpB;AACA;EACE,gBAAgB;AAClB","sourcesContent":["<template>\r\n  <div class=\"text normal\">\r\n    <footer>\r\n      <small @click=\"change\">{{ text }}</small>\r\n      <br />\r\n      <a href=\"https://github.com/TheVeryDarkness/website_dragonlion_sport/\"\r\n        >Maintain</a\r\n      >ed by\r\n      <a href=\"mailto:1853308@tongji.edu.com\">黄博奕</a>\r\n      <br />People's Republic of China\r\n    </footer>\r\n  </div>\r\n</template>\r\n\r\n<script lang=\"ts\">\r\nimport { get, set } from \"@/localStorage\";\r\n// Non-lazy\r\nimport \"~/css/basic.css\";\r\n// lazy\r\nimport { default as dark } from \"~/css/dark.css\";\r\nimport { default as light } from \"~/css/light.css\";\r\nimport { defineComponent } from \"vue\";\r\nlight.use();\r\ndark.use();\r\n{\r\n  var curtainStyle = document.getElementById(\"curtain\");\r\n  if (curtainStyle) {\r\n    curtainStyle.remove();\r\n  } else console.error(\"No curtain style\");\r\n}\r\n\r\nfunction getDefault() {\r\n  const s = get(\"theme\");\r\n  if (s == \"dark\") {\r\n    return false;\r\n  } else if (s == \"light\") {\r\n    return true;\r\n  }\r\n\r\n  if (window.matchMedia) {\r\n    if (window.matchMedia(\"(prefers-color-scheme: dark)\").matches) {\r\n      return false;\r\n    } else if (window.matchMedia(\"(prefers-color-scheme: light)\").matches) {\r\n      return true;\r\n    } else if (\r\n      window.matchMedia(\"(prefers-color-scheme: no-preference)\").matches\r\n    ) {\r\n      return true;\r\n    }\r\n  }\r\n\r\n  {\r\n    const date = new Date();\r\n    if (date.getHours() >= 22 || date.getHours() <= 6) return false;\r\n    else return true;\r\n  }\r\n}\r\n\r\nconst footer = defineComponent({\r\n  data: () => {\r\n    return {\r\n      text: \"本来无物何处尘埃\",\r\n      enlightened: getDefault(),\r\n    };\r\n  },\r\n  methods: {\r\n    change() {\r\n      if (this.enlightened) {\r\n        this.turnOff();\r\n        set(\"scheme\", \"dark\");\r\n      } else {\r\n        this.turnOn();\r\n        set(\"scheme\", \"light\");\r\n      }\r\n    },\r\n    turnOn() {\r\n      dark.unuse();\r\n      light.use();\r\n      this.enlightened = true;\r\n      this.text = \"一开灯一切变妄念\";\r\n    },\r\n    turnOff() {\r\n      dark.use();\r\n      light.unuse();\r\n      this.enlightened = false;\r\n      this.text = \"一熄灯一切被纪念\";\r\n    },\r\n  },\r\n});\r\nexport default footer;\r\n</script>\r\n\r\n<style scoped>\r\nfooter {\r\n  color: inherit;\r\n  vertical-align: bottom;\r\n  text-align: center;\r\n}\r\nsmall {\r\n  font-size: 0.8em;\r\n}\r\n</style>"],"sourceRoot":""}]);
