<template>
  <div v-for="(value, key) in node" :key="key">
    <div v-if="key != 'sub'">
      <label
        v-show="editable(key)"
        style="cursor: pointer"
        @click="deleteKey"
        v-bind:for="'input-value-' + key"
      >
        -
      </label>
      <label v-show="!editable(key)" style="cursor: not-allowed"> - </label>
      <input size="6" v-if="key" v-bind:value="key" readonly />
      <input
        v-bind:id="'input-value-' + key"
        size="32"
        @input="updateValue(key, $event)"
        v-bind:value="JSON.stringify(node[key])"
      />
    </div>
  </div>
  <label v-show="!!new_key" style="cursor: pointer" @click="addKey"> + </label>
  <label v-show="!new_key" style="cursor: not-allowed"> + </label>
  <input size="6" v-model="new_key" />
</template>

<script lang="ts">
import { defineComponent, PropType } from "vue";
import { NodeBasic } from "@/tree";
const editor = defineComponent({
  data() {
    return { new_key: "" };
  },
  props: { node: { type: Object as PropType<NodeBasic>, required: true } },
  emits: ["update"],
  computed: {},
  methods: {
    addKey() {
      this.$emit("update", this.new_key, "");
      this.new_key = "";
    },
    deleteKey(e: MouseEvent) {
      const elem = e.target as HTMLElement;
      const key = (elem.parentNode?.childNodes[2] as HTMLInputElement).value;
      this.$emit("update", key, undefined);
    },
    updateValue(key: string, e: InputEvent) {
      const input = e.target as HTMLInputElement;
      const label = input.labels[0];
      try {
        const value = input.value;
        const obj = JSON.parse(value);
        this.$emit("update", key, obj);
        label.textContent = " &#215; ";
      } catch (error) {
        label.textContent = " &#215; ";
        console.error(e, "does not contain legal JSON value.");
      }
    },
    editable(key: string) {
      return key != "value" && key != "sub";
    },
    legal(str: string) {
      try {
        const s = JSON.parse(str);
        return true;
      } catch (e) {
        return false;
      }
    },
  },
});
export default editor;
</script>

